@import '../../common/main.scss';

.backdrop {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 10;
}

.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  padding: 30px 60px 60px;
  font-size: 20px;
  font-weight: 600;
  border-radius: 5px;
  color: $secondaryTextColor;
  background-color: $secondaryBgColor;
  box-shadow: 0px 1px 3px rgb(0 0 0 / 12%), 0px 1px 1px rgb(0 0 0 / 14%),
    0px 2px 1px rgb(0 0 0 / 20%);
}

.modal-choice {
  width: 170px;
  display: flex;
  justify-content: space-between;
}

.modal-choice input[type='radio'] {
  display: none;
}

.modal-choice label {
  width: 70px;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 40px;
  color: $accentColor;
  border-radius: 3px;
  box-shadow: 0px 1px 3px rgb(0 0 0 / 12%), 0px 1px 1px rgb(0 0 0 / 14%),
    0px 2px 1px rgb(0 0 0 / 20%);
  cursor: pointer;
}

.modal-choice input[type='radio']:checked + label {
  color: $primaryTextColor;
  background-color: $accentColor;
}

.modal-close {
  position: absolute;
  top: 8px;
  right: 8px;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: 1px solid rgba(0, 0, 0, 0.1);
  background: $secondaryBgColor;
  color: $accentColor;
  cursor: pointer;
}
